import { Component, OnInit } from '@angular/core';
import { TmdbService } from "src/app/servicios/tmdb.service";
import { log } from 'util';

@Component({
  selector: 'app-mi-lista',
  templateUrl: './mi-lista.component.html',
  styleUrls: ['./mi-lista.component.css']
})
export class MiListaComponent implements OnInit {
  milista = [];

  constructor(private tmdb: TmdbService) {
    this.tmdb.getList().subscribe((data: any) => {
      this.milista.push(data);
      console.log(this.milista);
      
    });
   }

  ngOnInit() {
  }

}
