import { Component, OnInit } from '@angular/core';
import { TmdbService } from "src/app/servicios/tmdb.service";

@Component({
  selector: 'app-inicio',
  templateUrl: './inicio.component.html',
  styleUrls: ['./inicio.component.css']
})
export class InicioComponent implements OnInit {

  
  recomendadas = []

  constructor(private tmdb: TmdbService) {
    this.tmdb.getTendenciasSemana().subscribe((data: any) => {
      this.recomendadas.push({
        "genero": "tendencias de la semana",
        "lista" : [data]
      })
    });
    this.tmdb.getPopulares().subscribe((data: any) => {
      this.recomendadas.push({
        "genero": "populares en netflics",
        "lista" : [data]
      })
    });
    this.tmdb.getProxEstrenos().subscribe((data: any) => {
      this.recomendadas.push({
        "genero": "Proximos estrenos",
        "lista" : [data]
      })
    });
    this.tmdb.getTendenciasDia().subscribe((data: any) => {
      this.recomendadas.push({
        "genero": "tendencias de hoy",
        "lista" : [data]
      })
    });
    
    console.log(this.recomendadas);
    
  }

  ngOnInit() {
  }

}
