import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { HomeComponent } from "./componentes/home/home.component";
import { PeliculasComponent } from './componentes/peliculas/peliculas.component';
import { CategoriasPeliculasComponent } from './componentes/categorias-peliculas/categorias-peliculas.component';
import { DetallePeliculaComponent } from './componentes/detalle-pelicula/detalle-pelicula.component';
import { LoginComponent } from './componentes/login/login.component';
import { MiListaComponent } from './componentes/mi-lista/mi-lista.component';
import { SeriesComponent } from './componentes/series/series.component';
import { DetalleSerieComponent } from './componentes/detalle-serie/detalle-serie.component';
import { initializeApp } from 'firebase';
import { InicioComponent } from './componentes/inicio/inicio.component';
import { BusquedaComponent } from './componentes/busqueda/busqueda.component';
import { DatoscuentaComponent } from './componentes/datoscuenta/datoscuenta.component';
import { CambiarplanComponent } from './componentes/cambiarplan/cambiarplan.component';
import { PlanseleccionadoComponent } from './componentes/planseleccionado/planseleccionado.component';
import { Error404Component } from './componentes/error404/error404.component';

const routes: Routes = [
  { path: "home", component: HomeComponent },
  { path: "peliculas", component: PeliculasComponent},
  { path: "categorias_peliculas", component: CategoriasPeliculasComponent},
  { path: "movie/:id", component: DetallePeliculaComponent},
  { path: "tv/:id", component: DetalleSerieComponent},
  { path: "login", component: LoginComponent},
  { path: "inicio", component: InicioComponent},
  { path: "milista", component: MiListaComponent},
  { path: "series", component: SeriesComponent},
  { path: "busqueda", component: BusquedaComponent},
  { path: "cuenta", component: DatoscuentaComponent},
  { path: "cambiar-plan", component: CambiarplanComponent},
  { path: "plan-seleccionado", component: PlanseleccionadoComponent},
  { path: "", pathMatch: "full", component: Error404Component },
  { path: "**", pathMatch: "full", component: Error404Component },
  
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule {}
