import { Component, OnInit } from "@angular/core";
import { TmdbService } from "src/app/servicios/tmdb.service";

@Component({
  selector: "app-busqueda",
  templateUrl: "./busqueda.component.html",
  styleUrls: ["./busqueda.component.css"]
})
export class BusquedaComponent implements OnInit {
  peliculas: any = [];
  generos: any = [];
  peliculasGenero: any = [];
  anios: number[] = [2010, 2011, 2012, 2013, 2014, 2015];
  generoAnterior: number = -1;

  constructor(private tmdb: TmdbService) {
    this.getGeneros();
    console.log(this.anios);
  }

  ngOnInit() {}

  getPeliculas(termino: string) {
    this.tmdb.getBusquedaPeliculas(termino).subscribe((data: any) => {
      console.log(data);
      this.peliculas = data;
    });
  }

  getGeneros() {
    this.tmdb.getGeneros().subscribe((data: any) => {
      console.log(data);
      this.generos = data.genres;
    });
  }
  getPeliculasGenero(genero: string, termino: string) {
    this.tmdb.getPeliculasGenero(genero, termino).subscribe((data: any) => {
      this.peliculas = [];
      console.log("data", data);
      let aux = data.results;
      this.peliculas = this.peliculas.concat(aux);
      console.log(this.peliculas);
    });
  }
  getPeliculasAnio(año: string) {
    this.tmdb.getPeliculasAño(año).subscribe((data: any) => {
      this.peliculas = [];
      console.log("data", data);
      let aux = data.results;
      this.peliculas = this.peliculas.concat(aux);
      console.log(this.peliculas);
    });
  }

  
}