import { Component, OnInit } from '@angular/core';
import { TmdbService } from "src/app/servicios/tmdb.service";
import { typeWithParameters } from "@angular/compiler/src/render3/util";
import { logWarnings } from 'protractor/built/driverProviders';
import {RouterModule, Routes, Router} from '@angular/router';
import { log } from 'util';

@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.css']
})
export class NavbarComponent implements OnInit {
  IsHidden= true;
  isCategorias= true;
  bottonCategorias= true;
  generos = [];
  constructor(private tmdb: TmdbService,private router:Router) {
    this.tmdb.getGenres().subscribe((data: any) => {
      this.generos = data.genres;
      console.log(this.generos);
    });
  }

  ngOnInit() {
  }

  onActive(valor: string){
    if(this.router.isActive(valor,true)){
      this.isCategorias = true;
      return false;
    } else{ 
      this.isCategorias = true;
      return true;
    }    
  }

  onActivePe(valor: string){
    if(this.router.isActive(valor,true)){
      return false;
    } else{ 
      return true;
    }    
  }
  onActiveNav(){
    if(this.router.isActive("/home",true) || this.router.isActive("/login",true)){
      return true;
    } else{ 
      return false;
    }    
  }

  onSelect(){
    this.IsHidden= !this.IsHidden;
    console.log(this.IsHidden);
   }
   buttonCategoria(){
    this.isCategorias= !this.isCategorias;
   }
}
